* {
  box-sizing: border-box;
}
.gamay {
  width: 78%;
  margin-left: auto;
  margin-right: auto;
  max-height: 100vh;
}

body {
  margin: 0;
  background-color: whitesmoke;
}
.invert {
  width: 80px;
  margin-left: 0;
  margin-top: 15%;
}
.container {
  display: flex;
  align-items: center;
  justify-content: center;
}

header {
  height: 100px;
  margin-left: auto;
  margin-right: auto;
  width: 70%;
  display: flex;
  align-items: center;
  background: rgb(131, 58, 180);
  background: linear-gradient(
    90deg,
    rgba(131, 58, 180, 1) 0%,
    rgba(253, 29, 29, 1) 50%,
    rgba(252, 176, 69, 1) 100%
  );
}
header > img {
  height: 80%;
  margin-left: 10%;
}

header > p {
  font-family: VT323, monospace;
  color: whitesmoke;
  font-size: 50px;
  margin-left: 60px;
}

.meme {
  position: relative;
  width: 90%;
  margin: auto;
}

.meme > img {
  width: 100%;
}

.meme > h2 {
  position: absolute;
  width: 80%;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 5px;
  font-family: impact, sans-serif;
  font-size: 2em;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow: 2px 2px 0 #000, -2px -2px 0 #000, 2px -2px 0 #000,
    -2px 2px 0 #000, 0 2px 0 #000, 2px 0 0 #000, 0 -2px 0 #000, -2px 0 0 #000,
    2px 2px 5px #000;
}

.meme > .bottom {
  bottom: 0;
}

.meme > .top {
  top: 0;
}

.meme-form {
  width: 70%;
  margin: 20px auto;
  display: flex;
  justify-content: space-between;
}

.meme-form > input {
  width: 100%;
  height: 40px;
}

.meme-form > button {
  border: none;
  font-family: VT323, monospace;
  font-size: 25px;
  letter-spacing: 1.5px;
  color: white;
  background: #6441a5;
}

.meme-form > input::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  font-family: VT323, monospace;
  font-size: 25px;
  text-align: cen;
}
.meme-form > input::-moz-placeholder {
  /* Firefox 19+ */
  font-family: VT323, monospace;
  font-size: 25px;
  text-align: cen;
}
.meme-form > input:-ms-input-placeholder {
  /* IE 10+ */
  font-family: VT323, monospace;
  font-size: 25px;
  text-align: cen;
}
.meme-form > input:-moz-placeholder {
  /* Firefox 18- */
  font-family: VT323, monospace;
  font-size: 25px;
  text-align: cen;
}
@media only screen and (max-width: 600px) {
  .gamay {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
  .meme-form > input {
    width: 100% !important;
    height: 40px;
  }
  .meme-form > input::placeholder {
    font-size: 15px;
  }

  header > p {
    font-size: 30px;
    margin-left: 30px;
  }
  .invert {
    margin-top: 30%;
  }
  header {
    width: 100%;
  }
  .meme-form {
    width: 94%;
  }
}
